@import "../../styles/abstracts/index.scss";

.footer {
  padding: 32px 16px;
  border-top: 1px solid $color-elements;

  &__container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    max-width: 1200px;
    margin: 0 auto;

    @include notMobile {
      flex-wrap: nowrap;
    }
  }

  &__logo {
    display: flex;
    width: 100%;
    margin-bottom: 32px;

    @include notMobile {
      width: 33%;
      margin-bottom: 0;
    }
  }

  &__navigation {
    width: 100%;

    @include notMobile {
      width: 33%;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;

    padding: 0;
    margin: 0;
    margin-bottom: 32px;
    gap: 16px;

    text-align: left;
    list-style: none;

    @include notMobile {
      flex-wrap: nowrap;
      flex-direction: row;
      justify-content: space-between;
      margin-bottom: 0;
      text-align: center;
    }
  }

  &__link {
    color: $color-white;
  }

  &__toTop {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;

    @include notMobile {
      width: 33%;
      justify-content: end;
    }
  }

  &__toTop-label {
    color: $color-secondary;
  }

  &__toTop-button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
    margin-left: 16px;
    background: $color-surface-2;
    border: none;
    cursor: pointer;

    &:hover {
      background: $color-icons;
    }
  }

  &__toTop-icon {
    color: $color-white;
  }
}
